.main-advantages {
  min-height: 562px;
  color: $color-tangaroa;
  position: relative;
  display: flex;
  justify-content: center;
  background-color: $color-solitude;

  @media (max-width: $vp-1365) {
    min-height: 588px;
  }

  @media (max-width: $vp-767) {
    min-height: 997px;
  }
}

.main-advantages__wrapper {
  width: $vp-1366;
  margin: 0 auto;
  display: grid;
  grid-row-gap: 60px;
  justify-content: center;
  align-items: center;
  align-self: center;
  z-index: 1;

  @media (max-width: $vp-1365) {
    width: $vp-768;
  }

  @media (max-width: $vp-767) {
    width: $vp-320;
    grid-row-gap: 40px;
  }
}

.main-advantages__image {
  position: absolute;
  z-index: 0;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-advantages__title {
  text-align: center;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin: 0;

  @media (max-width: $vp-1365) {
    font-size: 26px;
    line-height: 39px;
  }

  @media (max-width: $vp-767) {
    font-size: 24px;
    line-height: 36px;
  }
}

.main-advantages__container {
  display: grid;
  grid-template-columns: repeat(3, 33%);
  grid-template-rows: 1fr;
  grid-column-gap: 20px;
  justify-content: center;
  margin: 0 auto;
  padding: 0;
  
  @media (max-width: $vp-1365) {
    grid-column-gap: 15px;
  }

  @media (max-width: $vp-767) {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 33%);
    grid-column-gap: none;
    grid-row-gap: 15px;
  }
}

.main-advantages__card {
  width: 360px;
  box-sizing: border-box;
  padding: 30px;
  background-color: $color-alice-blue;
  display: grid;
  grid-template-rows: 36px auto auto;
  align-items: center;

  @media (max-width: $vp-1365) {
    width: 206px;
    padding: 25px 20px;
  }

  @media (max-width: $vp-767) {
    width: 290px;
  }
}

.main-advantages__header {
  margin: 18px 0 20px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  align-self: baseline;

  @media (max-width: $vp-1365) {
    line-height: 26px;
    margin: 10px 0;
  }

  @media (max-width: $vp-767) {
    line-height: 25px;
    margin: 19px 0;
  }
}

.main-advantages__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
  align-self: baseline;
}
